import React from 'react'
import crossIcon from './imgs/cross-icon-dark.png';
import checkIcon from './imgs/check-icon.png';
import { useAuth } from '../../AuthProvider';
import { useNavigate } from 'react-router-dom';
import { useUsers } from '../../UsersInfoProvider';

export default function SuccessModal({succMsg, succModalVisible, setSuccModalVisible, navigate, popupEmailRef}) {
  

  const {changeAccStats, allUsers, setIsAdmin} = useUsers();
   

  const buttonClickHandler = () => {

    if (succMsg.includes("Sign in")) {
      
      const userEmail = localStorage.getItem('email');
      changeAccStats(userEmail, true); 
      
      //activate email pop up
      popupEmailRef.current.classList.add("active");
      
    } 

    else if (succMsg.includes("logout")) {
      navigate('/');
    }

    else {  
      navigate('/login'); 
    }
    setSuccModalVisible(false);
  }

  return (
    <>
    <div className={` fixed ${succModalVisible ? "block" : "hidden"} inset-0 bg-cartOverlay z-40`}></div>

    <div className={`bg-white succ-modal-card text-black w-5/6 fixed z-40 rounded-xl flex flex-col justify-center items-center gap-0 ${succModalVisible ? "active" : ""}`}>

      <div>
        <p className='text-msg font-semibold text-dark text-center'>{succMsg}</p>
      </div>
      <div>
        <button onClick={buttonClickHandler} className='p-2 px-6 bg-brown text-white  border-2 border-solid border-brow rounded-full'>{succMsg.includes("Registered") ? "sign in" : "continue"}</button>
      </div>
      <img onClick={() => setSuccModalVisible(false)} className={`absolute ${succMsg.includes("Sign in") ? "hidden" : ""} absolute top-2 right-2 cursor-pointer`} draggable="false" width={40} alt='' src={crossIcon} />
      <img alt='' className={`absolute top-4 check`} width={35} src={checkIcon} draggable="false" />
    </div>
    </>
    
  )
}
