import React from 'react'
import { useNavigate } from 'react-router-dom';
import { Link } from 'react-router-dom';
import { useAuth } from '../../AuthProvider';
import homeImg from './imgs/home-icon.png'
import productImg from './imgs/product-icon.png';
import phoneImg from './imgs/phone-icon.png';
import userIcon from './imgs/user-icon.png';
import crossImg from './imgs/cross-icon.png';
import logoutIcon from './imgs/logout-icon.png';
import clockIcon from './imgs/clock-icon.png';
import websiteIcon from './imgs/website-icon.jpg';
import AccountAvatar from '../user info/AccountAvatar';
import { useUsers } from '../../UsersInfoProvider';
import adminIcon from './imgs/admin-panel-icon.png'

export default function MenuList({setMenuVisible, menuVisible, setLogoutVisible, setPurchaseVisible}) {
  const {userEmail, userName} = useAuth();
  const {isLogin, isAdmin} = useUsers();
  const navigate = useNavigate();
  const navEvent = () => {
    setMenuVisible(false);
  }

  const handleLogout = () => {
    setLogoutVisible(true)
    setMenuVisible(false);
  }

  return (
    <>
    <div onClick={() => setMenuVisible(false)} className={`fixed ${!menuVisible && "hidden"} inset-0 bg-cartOverlay w-full h-screen z-40  md:hidden`}></div>
    <div className={`menuList-con ${menuVisible && "active"} fixed top-0 bottom-0 bg-darkBrown flex gap-10 items-start flex-col z-40 py-16 md:hidden`}>
        
        <img width={30} onClick={() => setMenuVisible(false)} className='absolute top-3 right-3 cursor-pointer' alt='' src={crossImg} draggable="false" />
    
        <div className='flex items-center gap-5 self-center flex-col userInfo-mobile w-full'>
       
          {
            isLogin ? <AccountAvatar size={50}/> : <img className='rounded-full' src={websiteIcon} alt='' width={50}/>
          }
          <p className='text-sm text-white'>{!userName ? (userEmail ? userEmail : "Comfoody.com") : userName}</p>
        </div>
    
          <ul className='flex flex-col gap-10 text-lg w-full items-start text-tWhite z-20'>
            <li onClick={navEvent} className='cursor-pointer w-full rounded-xl relative flex items-center gap-2'>
              <img width={20} src={homeImg} alt=''/><Link to={'/'}>Home</Link>
            </li>
    
            <li onClick={navEvent} className='cursor-pointer  w-full rounded-xl relative flex items-center gap-2'>
              <img width={20} src={productImg} alt=''/><Link to={'/products'}>Products</Link>
            </li>
    
            <li onClick={navEvent} className='cursor-pointer w-full rounded-xl relative flex items-center gap-2'>
              <img width={20} src={phoneImg}  alt=''/><Link to={'/about-us'}>About us</Link>
            </li>

            {isLogin && <li onClick={() => {setPurchaseVisible(true); setMenuVisible(false);}} className='cursor-pointer  w-full rounded-xl relative flex items-center gap-2'>
              <img width={20} src={clockIcon} alt='' />
              <p>My Purchase</p>
            </li>}

            {
              isAdmin && (
                <li className='cursor-pointer  w-full rounded-xl relative flex items-center gap-2' onClick={() => {navigate('/admin-panel'); setMenuVisible(false)}}>
                  <img width={20} src={adminIcon} alt=''/>
                  <p>Admin Panel</p> 
                </li>
              )
            }

            {
              isLogin ? <li onClick={handleLogout} className='cursor-pointer w-full rounded-xl flex items-center gap-2'>
                <img width={15} src={logoutIcon} alt=''/><p>Logout</p>
              </li> : (
                <div className='flex gap-5 mt-5'>
                 <li onClick={(e) => {navEvent(e); navigate("/login");}} className='cursor-pointer relative bg-white text-brown text-base p-4 py-2 rounded-lg'>Login</li>
                 <li onClick={(e) => {navEvent(e); navigate("/register")}} className='cursor-pointer relative border-white border-2 border-solid text-base p-3 py-2 rounded-lg'>Register</li>
                </div>
              )
            }

            
          </ul>
          <p className='absolute bottom-4 left-6 text-sm text-white'>&copy; Comfoody {new Date().getFullYear()}</p>
        </div>
    </> 
  )
}
