import React from 'react'

export default function CircularLoader() {
  return (
    <div style={{boxShadow: 'none'}} className='mt-7 xl:mt-16'> 
        <l-line-spinner
          size="50"
          stroke="5"
          speed="1"   
          color="#4E3B34" 
        ></l-line-spinner>
    </div>
  )
}
