import React, { createContext, useContext, useState } from 'react'
import useWebSocket from 'react-use-websocket'
import { useAuth } from './AuthProvider';
import { useStocks } from './StocksProvider';

const UsersProvider = createContext();

export default function UsersInfoProvider({children}) {
  
  const {stocks} = useStocks();
  const [allUsers, setAllUsers] = useState([]);
  const [modifiedToggle, setModifiedToggle] = useState(false); 
  const [isLogout, setIsLogout] = React.useState(false);


  const isLogin = allUsers.some(data => {
    const userEmail = localStorage.getItem('email');
    return data.email === userEmail && data.status === true;
  });

  const isAdmin = allUsers.some(data => {
    const id = localStorage.getItem('unique-id');
    console.log(data._id, id);
    return data._id === id && data.personalInfo.role === "admin";
  })
  


  const [updateUsers, setUpdateUsers] = React.useState(false);
  /* To watch the changes in stocks and manipulate the user's cart */

  React.useEffect(() => {

    if (!!updateUsers) {

      sendJsonMessage({category: 'CartPro', updatedUsers: allUsers})
      setUpdateUsers(false);
    }

  }, [updateUsers])

  React.useEffect(() => {
    let changeChecker = false;
    setAllUsers(prev => {
      const arr = [...prev];

      for (let i = 0; i < arr.length; i++) {
      
        for (let j = 0; j < arr[i].cartProducts.length; j++) {
          const isAvail = stocks.some(data => {
            return data.index ===  arr[i].cartProducts[j].proIndex;
          });
        
          if (!isAvail) {
            arr[i].cartProducts.splice(j);
            changeChecker = true;
          }
         
        }

      }

      return arr;
    })

    if (!!changeChecker) {
      setUpdateUsers(true)
    }

  }, [stocks.length]);

  const addProCart = (newProduct) => {
    const userEmail = localStorage.getItem('email');

    setAllUsers(prev => {
       const arr = [...prev];

       for (let i = 0; i < arr.length; i++) {
         if (arr[i].email === userEmail) {
          arr[i].cartProducts = [...prev[i].cartProducts, newProduct]
         }
       }

      return arr;
    })

  }

  const changeAccStats = (email, stats) => {
    setModifiedToggle(true)
    setIsLogout(!stats)

    setAllUsers(prev => {

      let newArr = [...prev]

      for (let i = 0; i < newArr.length; i++) {
        if (newArr[i].email === email) {
          newArr[i].status = stats;
        } 
      }

      return newArr;

    })
  }

  const changePersonalInfo = (oldEmail, personalInfo) => {
    setModifiedToggle(true)
    setAllUsers(prev => {

      let arr = [...prev];

      for (let i = 0; i < arr.length; i++) {
  
        if (arr[i].email === oldEmail) {
          arr[i].personalInfo = {...personalInfo};
        }

      }

      return arr;

    })

  }



  React.useEffect(() => {

    fetch('/get-all-users', {
        method: "GET",
        headers: {
            "Content-Type": "application/json"
        }
    })
    .then(data => data.json())
    .then(data => {
        setAllUsers(data)
    })

  }, [])
  
  const uri = 'ws://localhost:1200';

  const {lastJsonMessage} = useWebSocket(uri, {
    share: true
  });

  const {sendJsonMessage} = useWebSocket(uri, {
    share: true,
    filter: () => false,
    retryOnError: true,
    shouldReconnect: () => true
  });

  
  React.useEffect(() => {

    if (!!modifiedToggle) {
      sendJsonMessage({category: 'users', modified: true, list: allUsers, isLogout})
  
      setModifiedToggle(false);
    }

  }, [modifiedToggle])

  React.useEffect(() => {

    if (!!allUsers.length) {
      sendJsonMessage({category: 'users', modified: false, list: allUsers})
    }

  }, [allUsers.length])

  const modifiedOrNot = (object, prop) => {
    if (object.hasOwnProperty(prop)) {
      setAllUsers(object.list);
      
      const acc = object.list.find(data => data.email === localStorage.getItem('email'));

      if (!!object.isLogout && (!!acc && !acc.status)) {
        localStorage.clear();
      }
    }
  }

  React.useEffect(() => {

    if (!!lastJsonMessage) {
      if (lastJsonMessage.hasOwnProperty('category')) {
        if (lastJsonMessage.category === 'users') {
        modifiedOrNot(lastJsonMessage, 'modified')
        }

        if (lastJsonMessage.category === 'CartPro') {
          setAllUsers(lastJsonMessage.updatedUsers);
        }
      }
    }

  }, [lastJsonMessage])

  return (
    <UsersProvider.Provider value={{allUsers,  setAllUsers, changeAccStats, changePersonalInfo, isLogin, isAdmin, addProCart}}>
      {
        children
      }
    </UsersProvider.Provider>
  )
}

export const useUsers = () => {
    return useContext(UsersProvider)
}
