import './App.css';
import React, {useRef} from 'react';
import CartCard from './components/products cart/cart';
import { useNavigate, Routes, Route, useParams} from 'react-router-dom';
import HomeSection from './components/home page/HomeSection';
import AboutUsSection from './components/about us page/AboutUsSection';
import ProductsSection from './components/products section/ProductsSection';
import Footer from './components/footer/Footer';
import LoginCard from './components/login-register/container';
import PageNotFound from './components/404 page/PageNotFound';
import NavSection from './components/navigation/NavSection';
import IndivPro from './components/Individual Product/IndivPro';
import AccountAuth from './components/user info/AccountAuth';
import MenuList from './components/navigation/MenuList';
import EmailPopup from './components/email popup/EmailPopup';
import RequireAuth from './RequireAuth';
import OrderAuth from './OrderAuth';
import UserModal from './components/user modal/UserModal';
import LogoutModal from './components/user modal/LogoutModal';
import SuccessModal from './components/login-register/SuccessModal';
import MyPurchase from './components/my purchase/MyPurchase';
import OrderSummary from './components/user info/OrderSummary';
import MainContainer from './components/admin panel/MainContainer';
import DashboardSection from './components/admin panel/DashboardSection';
import EditProducts from './components/admin panel/EditProducts';
import UsersSection from './components/admin panel/InfoTableSection';
import AllOrders from './components/admin panel/AllOrders';
import InvProData from './data/InvProData';
import { useUsers } from './UsersInfoProvider';
import UserInfoModal from './components/admin panel/UserInfoModal';
import MessageBarModal from './components/admin panel/MessageBarModal';
import crossIcon from './imgs/cross-icon.png'
import ErrorIcon from './imgs/error-icon.png'
import CheckAnimationModal from './components/modal materials/CheckAnimationModal';
import OrderModal from './components/admin panel/OrderModal';
import AdminPanelAuth from './components/admin panel/AdminPanelAuth';
import { useUpOrders } from './UpOrdersProvider';
import CircularLoader from './components/loaders/CircularLoader';
import { useStocks } from './StocksProvider';
import InvProProtection from './components/Individual Product/InvProProtection';

function App() {

  const navigate = useNavigate();
  const [menuVisible, setMenuVisible] = React.useState(false);
  
  const screenSize = window.screen.width;

  //user info in order section
  const [userPersonalInfo, setUserPersonalInfo] = React.useState({
    fullName: '', phoneNumber: '', email: '', address: '', zip: '', city: ''
  });
  const {allUsers, isLogin} = useUsers();
  const {stocks} = useStocks();

  
  /* User product quantity */
  const [proQuantity, setProQuantity] = React.useState(1);
  const orderEvent = ({target}) => {
    const pageText = target.id.toLowerCase();
    navigate(`/products/${pageText}`);
  }

  const [productsCart, setProductsCart] = React.useState([]);
  const [cartVisible, setCartVisible] = React.useState(false);
  const [buyNowPro, setBuyNowPro] = React.useState({totalProPrice: 0, proName: ""});

  //pop-up email 
  const popupEmailRef = useRef();

  //User Info Modal 
  const [userModalVisible, setUserModalVisible] = React.useState(false);

  //Logout Modal
  const [logoutVisible, setLogoutVisible] = React.useState(false); 

  

  React.useEffect(() => {
    //const storedProducts = JSON.parse(localStorage.getItem('products')) || [];
    const userEmail = localStorage.getItem('email');

    if (!!allUsers.length) {

      if (!userEmail || !isLogin) return;

      const {cartProducts: storedProducts} = allUsers.find(data => {
        return userEmail === data.email;
      });

      setProductsCart(storedProducts);
    }
    
  
  }, [allUsers, stocks.length])

  const [succModalVisible, setSuccModalVisible] = React.useState(false);
  const [succMsg, setSuccMsg] = React.useState("");

  //Purchase Section Visibility
  const [purchaseVisible, setPurchaseVisible] = React.useState(false);
  
  //Upcoming Products
  const [upcomingProducts, setUpcomingProducts] = React.useState([]);
  upcomingProducts.sort((a, b) => upcomingProducts.indexOf(b) - upcomingProducts.indexOf(a));

  //order summary visibility
  const [orderSummaryAct, setOrderSummaryAct] = React.useState(false);

  //active order summary product
  const [summaryProIndex, setSummaryProIndex] = React.useState(0);

  const [notifBarVisible, setNotifBarVisible] = React.useState(false);
  const [notifBarMsg, setNotifBarMsg] = React.useState("Product updated");
  
  

  /* User Modal States */
  const [isError, setIsError] = React.useState(false)
  
  const [userModLoad, setUserModLoad] = React.useState(false);
  const [isCheck, setIsChecked] = React.useState(false);
  const [succMailModal, setSuccMailModal] = React.useState(false);
  const [activeAcc, setActiveAcc] = React.useState({personalInfo: {}});
  const [modalVisible, setModalVisible] = React.useState(false);

  const [modalMsg, setModalMsg] = React.useState("");
  const closeMailModal = () => {
    setIsChecked(false);
    setSuccMailModal(false)
  }

  const errorModal = (
    <>
      <div onClick={() => setIsError(false)} className={`fixed inset-0 bg-cartOverlay w-full h-full z-30 ${!isError && "hidden"}`}></div>
      <div className={`transition duration-300 fixed bottom-1/2 right-1/2 translate-x-1/2 translate-y-1/2 bg-white rounded-md z-30 p-5 flex flex-col gap-4 items-center w-3/4 ${!!isError ? "scale-1 opacity-1" : "scale-0 opacity-0"} xsm:w-80`}>
        <img src={ErrorIcon} alt=''/>
        <div className='text-dark flex flex-col items-center'>
          <p className='text-center xl:text-lg'>Please provide an enough information</p>
          <button onClick={() => setIsError(false)} className='text-sm py-2 mt-3 rounded-md px-4 border-dark border-solid border xl:text-base'>continue</button>
        </div>

      </div>
    </>
  )

  const mailModal = (
    <>
      <div onClick={closeMailModal} className={`${!succMailModal && "hidden"} bg-cartOverlay fixed inset-0 h-full w-full z-40`}></div>
      <div className={`${succMailModal && "active"} succ-mail-modal bg-white fixed bottom-1/2 right-1/2 translate-x-1/2 translate-y-1/2 flex flex-col items-center justify-center rounded-md w-11/12 z-40 h-48 py-3 xsm:w-80`}>
        <img onClick={closeMailModal} className='absolute top-3 right-3 cursor-pointer' src={crossIcon} alt=''/>
        <div className='relative h-12'>
          {isCheck && <CheckAnimationModal />}
        </div>
        
        <div className='flex flex-col items-center gap-5'>
          <h2 className='text-3xl opaci font-semibold text-dark'>{modalMsg}</h2>
          <button onClick={closeMailModal} className='bg-brown p-2 text-white rounded-lg px-5'>continue</button>
        </div>
        
      </div>
    </>
  )

  const viewDetails = (data) => {
    setActiveAcc(data);
    setModalVisible(true)  
}

const [activeOrder, setActiveOrder] = React.useState({
  email: '',
  personalInfo: {}, 
  order: {products: []}
})

const [orderVisibleModal, setOrderModalVisible] = React.useState(false);

const viewOrderDetails = (data) => {
  setActiveOrder(data);
  setOrderModalVisible(true)
}

const {upOrders} = useUpOrders();
const [activeOrderModal, setActiveOrderModal] = React.useState({
  grandTotal: 0,
  products: [],
  date: ''
});

const {isProAvail} = useStocks();

/* Date Of Arrival Modal */
const [arrivedVisible, setArrivedVisible] = React.useState(false);
const [activeArrived, setActiveArrived] = React.useState({});
const [arrDate, setArrDate] = React.useState("");
const {statusUpChanger} = useUpOrders(); 
const [arrDateErr, setArrDateErr] = React.useState(false);
const acceptOrder = ({target}) => {

  if (!arrDate) {
    setArrDateErr(true);
    return}; 

  const {email, orderId, stats} = activeArrived;

  statusUpChanger(email, orderId, stats, arrDate)
    .then(() => {
      setNotifBarVisible(true);
      setNotifBarMsg(`Order ${stats ? "Accepted" : "Declined" }.`);
      setOrderModalVisible(false);
      setArrivedVisible(false);
      setArrDate("")
    })

}

const getArrivalDate = ({target}) => {
  setArrDate(target.value); 
  setArrDateErr(false);
  
}

const arrivalModal = (
  <>
  <div onClick={() => {setArrivedVisible(false); setArrDateErr(false)}} className={`${!arrivedVisible && "hidden"} bg-cartOverlay fixed inset-0 z-30`}></div>
  <div className={`${!arrivedVisible && "opacity-0 scale-0"} transition-all duration-500 flex flex-col gap-8 items-center rounded-lg p-10 bg-white w-11/12 fixed origin-bottom top-1/2 left-1/2 -translate-y-1/2 -translate-x-1/2 z-30 xsm:w-96`}>

   <img onClick={() => {setArrivedVisible(false); setArrDateErr(false)}} draggable={false} className='cursor-pointer absolute top-2 right-2' width={23} src={crossIcon} alt=""/>

    <h2 className='text-2xl font-semibold'>Date of arrival</h2>
    <div className='flex flex-col relative gap-3 items-center'>
      <p className={`absolute ${!arrDateErr && "opacity-0"}  font-semibold text-red -top-1 -translate-y-full`}>*input a date</p>
      <input value={arrDate} onChange={getArrivalDate} type='date' className={`border-2 p-3 rounded-md ${arrDateErr ? "border-red" : "border-brown"}`}/>
      <button onClick={acceptOrder} className='bg-brown text-white p-2 px-6 rounded-md'>continue</button>
    </div>
  </div>
  </>
 )

  return (
    <>
    {screenSize < 1550 && <MyPurchase setActiveOrderModal={setActiveOrderModal} setSummaryProIndex={setSummaryProIndex} setOrderSummaryAct={setOrderSummaryAct} setUpcomingProducts={setUpcomingProducts} upcomingProducts={upcomingProducts} purchaseVisible={purchaseVisible} setPurchaseVisible={setPurchaseVisible} />}
    <OrderModal setActiveArrived={setActiveArrived} setArrivedVisible={setArrivedVisible} setNotifBarVisible={setNotifBarVisible} setNotifBarMsg={setNotifBarMsg} activeOrder={activeOrder} setOrderModalVisible={setOrderModalVisible} orderVisibleModal={orderVisibleModal}/>
    
    <div className='main-wrapper pb-0 flex flex-col gap-0 md:px-10 xl:px-0 xl:gap-0 xl:pb-0 relative'>
      {
        cartVisible ? <CartCard  
                        navigate={navigate} 
                        setProductsCart={setProductsCart} 
                        proQuantity={proQuantity} 
                        productsCart={productsCart} 
                        setCartVisible={setCartVisible} /> : null
      }
       {/* Menu Tab for Mobile Devices */}
      <MenuList setPurchaseVisible={setPurchaseVisible} setMenuVisible={setMenuVisible} setLogoutVisible={setLogoutVisible} menuVisible={menuVisible}/>
      <EmailPopup popupEmailRef={popupEmailRef}/>
      <UserModal setPurchaseVisible={setPurchaseVisible} userModalVisible={userModalVisible} setUserModalVisible={setUserModalVisible} setLogoutVisible={setLogoutVisible}/>
      <LogoutModal setSuccMsg={setSuccMsg} setSuccModalVisible={setSuccModalVisible} logoutVisible={logoutVisible} setLogoutVisible={setLogoutVisible} setUserModalVisible={setUserModalVisible}/>

      
      {screenSize >= 1550 && <MyPurchase setActiveOrderModal={setActiveOrderModal} setSummaryProIndex={setSummaryProIndex} setOrderSummaryAct={setOrderSummaryAct} setUpcomingProducts={setUpcomingProducts} upcomingProducts={upcomingProducts} purchaseVisible={purchaseVisible} setPurchaseVisible={setPurchaseVisible} />}

      <OrderSummary setSuccMailModal={setSuccMailModal} setIsChecked={setIsChecked} setModalMsg={setModalMsg}  activeOrderModal={activeOrderModal} orderSummaryAct={orderSummaryAct} setOrderSummaryAct={setOrderSummaryAct}/>
      
       <div className={`flex justify-center w-full h-screen fixed inset-0 -z-20 items-center ${succModalVisible && 'z-40'}`}>
         <SuccessModal navigate={navigate}
                       setSuccModalVisible={setSuccModalVisible}
                       succModalVisible={succModalVisible}
                       setSuccMsg={setSuccMsg}
                       succMsg={succMsg}/>
       </div>
      
       <UserInfoModal 
         setLoader={setUserModLoad}
         setIsError={setIsError} 
         listLen={allUsers.length} 
         setNotifBarVisible={setNotifBarVisible} 
         setNotifBarMsg={setNotifBarMsg}
         setUserModLoad={setUserModLoad} 
         setIsChecked={setIsChecked} 
         setSuccMailModal={setSuccMailModal} 
         setModalMsg={setModalMsg}
         activeAcc={activeAcc} 
         userModalVisible={modalVisible} 
         setUserModalVisible={setModalVisible}
         />
       {errorModal}
       {mailModal}
       {arrivalModal}
       

      <Routes>
       

          <Route path='/' element={ 
         <>
            <NavSection userModalVisible={userModalVisible} setUserModalVisible={setUserModalVisible} setMenuVisible={setMenuVisible} setCartVisible={setCartVisible}   productsCart={productsCart}/>
            <HomeSection orderEvent={orderEvent}/>
            <Footer/> 
          </> 
          }/>


          {/* Admin Panel */}
          <Route path='/admin-panel' element={<AdminPanelAuth>
            <MainContainer notifBarVisible={notifBarVisible} notifBarMsg={notifBarMsg} setNotifBarVisible={setNotifBarVisible} />
          </AdminPanelAuth>}>
            <Route index element={<DashboardSection viewOrderDetails={viewOrderDetails} viewDetails={viewDetails} orders={upOrders}/>}/>
            <Route path='products' element={(
              <EditProducts isUser={false} setNotifBarVisible={setNotifBarVisible} setNotifBarMsg={setNotifBarMsg}/>)}/>
            <Route path='users' element={<UsersSection list={allUsers} viewDetails={viewDetails}
         userModLoad={userModLoad} category={"Users"}  isUser={true} />} />
            <Route path='orders' element={<AllOrders viewOrderDetails={viewOrderDetails} orders={upOrders} category={"Orders"} />} />
          </Route>

          {/* Contacts Section */}
          <Route path={'/about-us'} element={
          <>
            <NavSection  userModalVisible={userModalVisible} setUserModalVisible={setUserModalVisible} setMenuVisible={setMenuVisible} setCartVisible={setCartVisible} productsCart={productsCart}/>
             <AboutUsSection />
            <Footer />
          </>
          }/>

          <Route path={'/products'} >
            <Route index element={
              <>
                <NavSection  userModalVisible={userModalVisible} setUserModalVisible={setUserModalVisible} setMenuVisible={setMenuVisible} setCartVisible={setCartVisible} productsCart={productsCart}/>
                  <ProductsSection orderEvent={orderEvent}/>
                  <Footer />
              </>
            }/> 

            <Route path=":proIndex" element={
                         isProAvail ? 
                                 <InvProProtection>
                                  <IndivPro 
                                     userModalVisible={userModalVisible} 
                                     setUserModalVisible={setUserModalVisible}
                                     navigate={navigate} 
                                     setMenuVisible={setMenuVisible}
                                     setCartVisible={setCartVisible}
                                     productsCart={productsCart} 
                                     setProductsCart={setProductsCart}
                                     orderEvent={orderEvent} 
                                     proQuantity={proQuantity} 
                                     setProQuantity={setProQuantity}  
                                     setBuyNowPro={setBuyNowPro}/> 
                                  </InvProProtection> :
                                  <div className='w-full h-screen flex justify-center items-center'>
                                  <CircularLoader />
                                </div>
                                }/>
           </Route>

        {/* Checkout */}
        <Route path={'/account-auth'} element={
        <OrderAuth productsCart={productsCart}>
          <NavSection userModalVisible={userModalVisible} setUserModalVisible={setUserModalVisible} setMenuVisible={setMenuVisible} setCartVisible={setCartVisible} productsCart={productsCart}/>
            <AccountAuth upcomingProducts={upcomingProducts} setProductsCart={setProductsCart} setUpcomingProducts={setUpcomingProducts} userPersonalInfo={userPersonalInfo} setUserPersonalInfo={setUserPersonalInfo} productsCart={productsCart}/>
          <Footer />
        </OrderAuth>}/>

        <Route path={'/account-auth-buynow'} element={
        <OrderAuth buyNowPro={buyNowPro} productsCart={productsCart}>
          <NavSection  userModalVisible={userModalVisible} setUserModalVisible={setUserModalVisible} setMenuVisible={setMenuVisible} setCartVisible={setCartVisible} productsCart={productsCart}/>
           <AccountAuth setUpcomingProducts={setUpcomingProducts} upcomingProducts={upcomingProducts} userPersonalInfo={userPersonalInfo} setUserPersonalInfo={setUserPersonalInfo}  buyNowPro={buyNowPro} 
           productsCart={productsCart}/>
          <Footer />
        </OrderAuth>}/>
         

        {/* Login and Register Section */}
        
        <Route path='/login' 
               element={
                <RequireAuth>
                  <LoginCard 
                  setUpcomingProducts={setUpcomingProducts}
                      setProductsCart={setProductsCart}
                      popupEmailRef={popupEmailRef}
                      />
                </RequireAuth>}/>
        <Route path='/register' 
               element={
                <RequireAuth>
                  <LoginCard 
                  setUpcomingProducts={setUpcomingProducts}
                      setProductsCart={setProductsCart}
                      popupEmailRef={popupEmailRef}
                      />
                </RequireAuth>}/>

        {/* Page not Found Route */}
        <Route path='*' element={<PageNotFound />}/>
      </Routes>
     
    </div>
    </>
  );
}

export default App;
