import React from 'react'
import { hourglass } from 'ldrs'

export default function LoadingPage() {
  hourglass.register();
  return (
    <div className='z-40 bg-cartOverlay fixed inset-0 flex justify-center items-center flex-col gap-3'>
      <l-hourglass
       size="50"
       bg-opacity="0.2"
       speed="1.75" 
       color="white" 
      ></l-hourglass>
      <p className='text-white text-xl'>Please wait...</p>
    </div>
  )
}
