import React, { createContext, useContext } from 'react'
import useWebSocket from 'react-use-websocket';

const ReviewsContext = createContext(null);

export default function ReviewsProvider({children}) {

    const [reviews, setReviews] = React.useState([]);
    const [isLoading, setIsLoading] = React.useState(false);
    const [replyLoading, setReplyLoading] = React.useState(false);
  
    React.useEffect(() => {
        fetch('/get-reviews', {
          method: "POST",
          headers: {
            "Content-Type": "application/json"
          }
        })
        .then(data => data.json())
        .then(data => {
          if (!!data) {
            setReviews(data)
          }
        })
      }, [])

    const uri = 'ws://localhost:1200';

    const {lastJsonMessage} = useWebSocket(uri, {
        share: true
    });

    const {sendJsonMessage} = useWebSocket(uri, {
        share: true,
        filter: () => false,
        retryOnError: true,
        shouldReconnect: () => true
    });


    const addReview = (data) => {
        /* setReviews(prev => [...prev, data]) */
        setIsLoading(true);
        sendJsonMessage(data)
    }

    const addReply = (index, replyData, _id) => {
      
      const data = {
        isReply: true,
        index,
        replyData,
        _id
      }
      sendJsonMessage(data)
    }

    const deleteReply = (outerIndex, innerIndex, proName) => {
      const data = {
        deleteReply: true,
        outerIndex,
        innerIndex,
        productName: proName
      }
    

      sendJsonMessage(data)
    }


    const removeReview = (data) => {
        setIsLoading(true);
        sendJsonMessage(data)
    }

    React.useEffect(() => {

        if (!!lastJsonMessage) {

            /* For adding comments */
            if (!!lastJsonMessage.proName) {
                setReviews(prev => [...prev, lastJsonMessage])
                setIsLoading(false)
            }

            else if (!!lastJsonMessage.delete) {
                const index = lastJsonMessage.index;
                setReviews(prev => [...prev].filter((data, origIndex) => origIndex !== index));
                setIsLoading(false)
            }

            else if (!!lastJsonMessage.isReply) {
              const {index, replyData} = lastJsonMessage;

              setReviews(prev => {
                /* const arr = [...prev].filter(data => {
                  return data.proName.trim().toLowerCase() === replyData.proName.trim().toLowerCase();
                }); */

                const arr = [...prev].filter(data => {
                  return data.proName === replyData.proName;
                });

                console.log(arr);
                
                for (let i = 0; i < arr.length; i++) {
                  if (i === index) {
                    arr[i].replies = [...arr[i].replies, replyData];
                  }

                }

                return arr;

                
              })
  
              setReplyLoading(false);
            } 

            else if (!!lastJsonMessage.hasOwnProperty("deleteReply")) {

              const {outerIndex, innerIndex, productName} = lastJsonMessage;

          
              setReviews(prev => {
 
                const arr = [...prev].filter(data => {
                  return data.proName === productName;
                });

                 for (let i = 0; i < arr.length; i++) {

                  if (i === outerIndex) {
                    arr[i].replies = [...arr[i].replies].filter((data, index) => index !== innerIndex);
                  }

                 }

                 return arr;
              })
              
            }
            
        }

    }, [lastJsonMessage])

    console.log(reviews);

  return (
   <ReviewsContext.Provider value={{addReview, reviews, removeReview, isLoading, addReply, deleteReply, replyLoading}} >
     {children}
   </ReviewsContext.Provider>
  )
}

export const useReviews = () => {
    return useContext(ReviewsContext)
}
