import React from 'react'
import CrossIcon from './imgs/cross-icon-dark.png'

export default function EmailVerification({emailVerVisible, setEmailVerVisible, fetchData, userOtp, setSuccModalVisible, setSuccMsg, userAdder, regUserInfo, setIsLoading, setRegUserInfo}) {
  const [validateCode, setValidateCode] = React.useState(['', '', '', '', '']);
  const [isErr, setIsErr] = React.useState(false);

  const handleInputOTP = ({target}) => {
   setValidateCode(prev => {
     const arr = [...prev];
     arr[target.id] = target.value;

     return arr;
     
   })
  }

 const inputs = []

 for (let i = 0; i < 5; i++) {

    inputs.push(<Inputs setIsErr={setIsErr} isErr={isErr} key={i} id={i} validateCode={validateCode} handleInputOTP={handleInputOTP} />)

 }

 const registerAcc = async () => {

   const result = await fetch('/add-register-acc', {
    method: "POST",
    headers: {
      "Content-Type": 'application/json'
    },
    body: JSON.stringify({email: regUserInfo.email, password: regUserInfo.password})
  })

  const data = await result.json();

  return data;
 }

 const compareOtp = () => {

  const isMatched = validateCode.join('') === userOtp;
  if (isMatched) {
     
     setValidateCode(['', '', '', '', ''])
     setEmailVerVisible(false);
     setIsLoading(true);

     registerAcc()
     .then((data) => {
      setIsLoading(false);
      setSuccMsg("Successfully Registered")
      setSuccModalVisible(true);
      userAdder(regUserInfo.email, false, data.isAdmin ? 'admin' : 'customer', data.uniqueId); 
      setRegUserInfo({email: '', password: '', confirmPass: ''})
     })
     
     
  } else {
    setIsErr(true)
  }
 }

  return (
    <>
    <div onClick={() => setEmailVerVisible(false)} className={` ${!emailVerVisible && "hidden"} fixed bg-cartOverlay z-40 inset-0 w-full h-full`}></div>
      <div className={` ${!emailVerVisible && "scale-0 opacity-0"} transition-all duration-500 fixed bottom-1/2 w-11/12 right-1/2 z-40 translate-x-1/2 translate-y-1/2 bg-white rounded-lg p-7 flex flex-col gap-8 xsm:w-4/5 sm:w-96 sm:p-10 sm:gap-10`}>
        <img onClick={() => setEmailVerVisible(false)} width={30} className='cursor-pointer select-none absolute top-2 right-2' src={CrossIcon} alt=''/>
         <div className='text-center flex flex-col gap-2 sm:gap-3'>
             <h2 className={`${!emailVerVisible && "opacity-0 -translate-y-10"} text-2xl font-semibold transition-all duration-500 ${emailVerVisible && "delay-300"} sm:text-3xl`}>Email Verification</h2>
             <p className={`${!emailVerVisible && "opacity-0 -translate-y-10"} text-sm text-gray duration-500 ${emailVerVisible && "delay-500"}`}>please enter your One-Time Password (OTP) that was sent to your email</p>
         </div>

         <div className={`${emailVerVisible && "active"} flex justify-between relative otp-inputs-con gap-2`}>
          {inputs}
          <p className={`${!isErr && "hidden"} font-bold absolute -bottom-7 text-red text-xs left-1/2 -translate-x-1/2`}>*OTP did not matched.</p>
         </div>

         <div className={`${emailVerVisible && "active"} flex flex-col items-center email-btn-con`}>
           <button className='bg-brown text-white px-8 py-2 rounded-md mb-5 text-sm transition-all sm:text-base' onClick={compareOtp}>continue</button>
           <p className='tetx-gray text-sm'>Didn't receive an email? <button className='underline text-darkBlue font-semibold transition-all' href="#">Resend</button></p>
         </div>
     </div>
    </>
  )
}


const Inputs = ({validateCode, handleInputOTP, id, isErr, setIsErr}) => {

  const inputValueHandler = ({target}) => {
    if (!!target.value) {
     const nextEl = target.nextElementSibling;
      if (!nextEl) return;

      nextEl.focus();
    }
    setIsErr(false)
   }

    return <input className={isErr && "error"} value={validateCode[id]} id={id} placeholder='X' onChange={(e) => {inputValueHandler(e); handleInputOTP(e);}} maxLength={1}/>
}
