import React from 'react'

export default function ErrorModal({errMsg, isVisible, setIsVisible}) {

  React.useEffect(() => {
   if (isVisible) {
    setTimeout(() => {
        setIsVisible(false);
    }, 3000)
   }
  })

  return (
    <>
      <div className={`z-40 ${isVisible ? "block" : "hidden"} fixed inset-0 bg-cartOverlay`}></div>
      <div className={`err-modal ${isVisible ? "active" : ""} z-50 bg-white w-4/5 fixed top-6 h-40 rounded-md text-black flex justify-center flex-col items-center px-5`}>
        <img src='./assets/Login Section/error-icon.png'/>
        <p className='text-center'>{errMsg}</p>
      </div>
    </>
  )
}
