import React from 'react'
import { NavLink} from 'react-router-dom';
import imgCart from './imgs/cart-icon.png';
import { useAuth } from '../../AuthProvider';
import userSrc from './imgs/user-icon.png';
import hatSrc from './imgs/chef-hat.png';
import AccountAvatar from '../user info/AccountAvatar';
import { useUsers } from '../../UsersInfoProvider';


export default function NavSection({userModalVisible, setMenuVisible, setCartVisible, productsCart, setUserModalVisible}) {
  const {isLogin} = useUsers();

  return (
    <header className='relative  text-brown flex justify-between items-center z-20 p-5 py-7 pb-0 sm:p-7  md:p-10 xl:px-24'>
        <div className='flex items-center gap-2'>
          <p className='text-2xl font-semibold'>Comfoody</p>
          <div className='w-8'>
           <img draggable='false' src={hatSrc}  alt='' className='w-12'/>
          </div>
        </div>
        
        <div  className='flex gap-6 md:hidden'>
           {
             isLogin ? (
               <div onClick={() => {setCartVisible(true); setUserModalVisible(false);}} className='w-8 relative cursor-pointer md:hidden'>
                {
                productsCart.length <= 0 ? null : <div className='bg-yellow text-sm font-bold flex justify-center items-center rounded-full absolute w-7 -right-4 -top-2'>{productsCart.length}</div>
              }
                 <img className='w-full select-none' draggable="false" src={imgCart} alt='' /> 
               </div>) :
               null
           }
   
           <div className="menu-con md:hidden" onClick={() => setMenuVisible(true)}>
              <div className="bar1"></div>
              <div className="bar2"></div>
              <div className="bar3"></div>
           </div>
        </div>
       
        
        <div className='hidden md:block'>
          <nav className='menu-list flex gap-3 items-center relative text-sm lg:gap-8 xl:gap-10'>
             <NavLink className='cursor-pointer relative' to={"/"}>Home</NavLink>
             <NavLink className='cursor-pointer relative' to={"/about-us"}>About us</NavLink>
             <NavLink className='cursor-pointer relative' to={"/products"}>Products</NavLink>
          </nav>
        </div>

        {
          isLogin ? (
            <div className='hidden md:flex gap-8'>
              <div onClick={() => {setCartVisible(true); setUserModalVisible(false)}} className='w-8 relative cursor-pointer'>
              {
                productsCart.length <= 0 ? null : <div className='bg-yellow text-sm font-bold flex justify-center items-center rounded-full absolute w-7 -right-4 -top-2'>{productsCart.length}</div>
              }
                <img className='w-full select-none' draggable="false" src={imgCart} alt='' /> 
              </div>
    
              <div onClick={() => setUserModalVisible(prev => !prev)} className='w-8 cursor-pointer relative'>
                 <i className={`${userModalVisible && "rotate-180"} select-none transition-all fa fa-chevron-down text-brown bg-grayBg absolute text-sm rounded-full px-1 -bottom-1 -right-2`}></i>
                <AccountAvatar size={30}/>
              </div>
           </div>
          ) :

          (
            <div className='hidden md:block'>
              <nav className='flex gap-5 text-sm items-center log-reg lg:gap-8'>
                 <NavLink to={'/login'} className='cursor-pointer login-btn'>Login</NavLink>
                 <NavLink to={'/register'} className='cursor-pointer rounded-full relative p-1 px-3 startBtn lg:p-2 lg:px-4'>Register</NavLink>
              </nav>
            </div>
          )
        }
      </header>
  )
}
