import React from 'react' 
import { GoogleLogin } from '@react-oauth/google';
import { jwtDecode } from "jwt-decode";
import { useAuth } from '../../AuthProvider';

export default function LoginCard({setForgotModalVisible, setSuccMsg, setIsLoading, setSuccModalVisible, signInfo, setSignInfo, showPass, navigate, setErrMsg, setIsVisible, setProductsCart, userAdder}) {

  const emailRef = React.useRef();
  const passRef = React.useRef();
  

  const {setUserEmail, setUserName, setGoogleId} = useAuth();
  

  const inputChange = (e) => {
    const {id, value} = e.target;
    setSignInfo(prev => (
      {
        ...prev,
        [id]: value
      }
    ))
    e.target.nextElementSibling.classList.remove('active')
  }
  
  const sendSignupInfo = () => {
    fetch('/signup-acc', {
      method: "POST",
      headers: {
        "Content-Type": "application/json"
      },
      body: JSON.stringify(signInfo)
    })
    .then(data => data.json())
    .then(data => {
      if (data.hasAccount) {
        if (data.passMatch) {
            localStorage.setItem('unique-id', data.userInfo._id);
            localStorage.setItem('email', data.email);
            
            setSuccMsg("Successfully Sign in");
            setSuccModalVisible(true);
            setIsLoading(false);
            
            userAdder(data.email, false, data.userInfo.personalInfo.role, data.userInfo._id);
           
          
        } else {
          passRef.current.nextElementSibling.classList.add('active');
          passRef.current.nextElementSibling.innerText = "*Incorrect password"
          setIsLoading(false);
        }
      } else {
        setErrMsg("We cannot find your account.")
        setIsVisible(true);
        setIsLoading(false);
      }
     
    })
  }


  const infoValidation = () => {
    const email = emailRef.current;
    const password = passRef.current;
    
    const emailMsg = email.nextElementSibling;
    const passMsg = password.nextElementSibling;

   
    if (!email.checkValidity()) {
      emailMsg.classList.add('active');
      const error = email.validationMessage.split('.');
      emailMsg.innerText = "*" + error[0];
      return;
    }

    if (!password.checkValidity()) {
      passMsg.classList.add('active');
      passMsg.innerText = "*" + password.validationMessage;
      return;
    }

    setIsLoading(true);
    //fetch function
    sendSignupInfo();

  }

 

  const UseGoogleAuth = (credential, id) => {
    const {email, name} = jwtDecode(credential);
    
    setIsLoading(true);
    
    fetch('/google-auth-acc', {
      method: "POST",
      headers: {
        "Content-Type": "application/json"
      },
      body: JSON.stringify({email: email})
    })
    .then(data => data.json())
    .then(data => {
      if (data.complete) {
          /* getProdutsInfo(email)
          .then(() => {
            
          }) */
            localStorage.setItem('unique-id', data.userInfo._id);
            setUserEmail(email);
            setUserName(name);
            setGoogleId(id);
            

            setSuccMsg("Successfully Sign in");
            setSuccModalVisible(true);
            userAdder(email, false, data.userInfo.personalInfo.role, data.userInfo._id);

            setIsLoading(false);
         

      }
    })
  }


  return (
    <div className='flex flex-col gap-8 h-full justify-center items-center login'>
           <h2 className='text-4xl font-bold'>Sign in</h2>
           {/* <ul className='flex gap-6'>
               <li onClick={login}><img className='cursor-pointer' width="35" alt='' src='./assets/fb-icon.png'/></li>
               <li><img className='cursor-pointer' width="35" alt='' src='./assets/instagram-icon.png'/></li>
               <li ><img className='cursor-pointer' width="35" alt='' src='./assets/tiktok-icon.png'/></li>
           </ul> */}
           <GoogleLogin 
             onSuccess={(res) => {
              
              UseGoogleAuth(res.credential, res.clientId)
            }}

             onError={(err) => {
              console.log(err)
             }}
             shape='pill'
             text='signin_with'
             cancel_on_tap_outside={true}
             
           />

           <div className='w-full input-box-con flex flex-col gap-10 items-center mt-2'>
             <div className='relative border-darkBlue w-10/12 h-12 rounded-xl'>
                <input ref={emailRef} onChange={inputChange} type='email' required id="email" className='absolute bg-transparent z-10 inset-0 w-full h-full px-2 rounded-xl'/>
                <p className='absolute opacity-0 -right-10 text-sm -top-6 text-red active'></p>
                <label className='absolute top-2 left-2'>Email</label>
             </div>

             <div className='relative border-darkBlue w-10/12 h-12 rounded-xl'>
             <img onClick={showPass} width="23" className='absolute top-3 right-3 z-20 cursor-pointer' src='./assets/Login Section/eye-slash.png' alt=''/>
                <input ref={passRef} onChange={inputChange} type='password' required id="password" className='absolute bg-transparent z-10 inset-0 w-full h-full px-2 rounded-xl'/>
                <p className='absolute opacity-0 -right-10 text-sm -top-6 text-red active'></p>
                <label className='absolute top-2 left-2'>Password</label>
                <i></i>
             </div>
           </div>

           <div className='flex items-center gap-5'>
           <button onClick={infoValidation} className='text-sm uppercase bg-brown text-white p-2 px-10 rounded-md'>Sign in</button>
           <button onClick={() => navigate('/register')} className='text-sm uppercase bg-white text-brown text- p-2 px-8 rounded-md "border-solid border-2 border-brown lg:hidden'>Register</button>
           </div>
           <p onClick={() => setForgotModalVisible(true)} className='text-textGray cursor-pointer underline'>Forgot your Password?</p>

        </div>
  )
}
