import React from 'react'
import { Navigate } from 'react-router-dom';
import { useLocation } from 'react-router-dom';

export default function OrderAuth({children, productsCart, buyNowPro}) {
   
  const {pathname} = useLocation();
  

  if (pathname === "/account-auth" && productsCart.length <= 0) {
        return <Navigate to='/'/>;
  } 
  
  else if (pathname === "/account-auth-buynow" && buyNowPro.proName === "") {
        return <Navigate to='/'/>;
  }
  
  else

  return children;
}
