import React, { useContext } from 'react'
import { createContext } from 'react'
import { useUsers } from './UsersInfoProvider';
import useWebSocket from 'react-use-websocket'
import { useAuth } from './AuthProvider';

const UpOrdersContext = createContext();

export default function UpOrdersProvider({children}) {

    
    const {allUsers, setAllUsers} = useUsers();
    const [usersUpdated, setUsersUpdated] = React.useState(false);

    const [numVisitors, setNumVisitors] = React.useState(0);

    const uri = 'ws://localhost:1200';
    const {lastJsonMessage} = useWebSocket(uri, {
     share: true
   });

    const {sendJsonMessage} = useWebSocket(uri, {
      share: true,
      filter: () => false,
      retryOnError: true,
      shouldReconnect: () => true
  });

  React.useEffect(() => {

    fetch('/monitor-visitors', {
      method: "POST",
      headers: {
        "Content-Type": "application/json"
      },
      body: JSON.stringify({})
    })
    .then(data => data.json())
    .then(data => {
      setNumVisitors(data.data.numVisitors);
      sendJsonMessage({numVisitors: data.data.numVisitors})
    })
    
  }, [])

  React.useEffect(() => {

    if ((!!lastJsonMessage && !!lastJsonMessage.numVisitors)) {
      setNumVisitors(lastJsonMessage.numVisitors)
    }

  }, [lastJsonMessage])
  

  React.useEffect(() => {

    if (!!lastJsonMessage) {
      if (!!lastJsonMessage.addOrder) {
        setAllUsers(lastJsonMessage.updatedUsers);
      }
    }

  }, [lastJsonMessage]) 

  const updateOrderDB = async (email, upPro) => {
    await fetch('/update-order-status', {
            method: 'POST',
            headers: {
                "Content-Type": "application/json"
            },
            body: JSON.stringify({email, upProducts: upPro})
          })
  }


  const updateOrderStats = async (email) => {
    const newUsers = [...allUsers];

    const {upcomingOrders} = newUsers.find(data => {
      return data.email === email;
    });

   await updateOrderDB(email, upcomingOrders);
  }


    React.useEffect(() => {

        if (usersUpdated) {
          
          sendJsonMessage({updatedUsers: allUsers, addOrder: true})

        setUsersUpdated(false)
        }

    }, [usersUpdated])

    const userWithOrder = allUsers.filter(data => {
        return data.upcomingOrders.length > 0
      });
      const upOrders = [];
      
      for (let i = 0; i < userWithOrder.length; i++) {
      
        for (let  j = 0; j < userWithOrder[i].upcomingOrders.length; j++) {
      
          upOrders
          .push({email: userWithOrder[i].email,
                 personalInfo: userWithOrder[i].personalInfo, 
                 order: userWithOrder[i].upcomingOrders[j],
                 _id: userWithOrder[i]._id,
                 isRealEmail: userWithOrder[i].hasOwnProperty("password") ? false : true,
                 orderId: j,
                })
        }
      
      }


      const accepted = [0];

      for (let i = 0; i < upOrders.length; i++) {
    
        if (upOrders[i].order.status === true) {
          accepted.push(upOrders[i].order.grandTotal);
        }
    
      }

      const totalSales =  accepted.reduce((total, num) => total + num);

    const statusUpChanger = async (email, id, status, arrDate = "") => {
        
        
        setAllUsers(prev => {
            const arr = [...prev];

            const acc = arr.find(data => {
                return data.email === email
            });
            
            for (let i = 0; i < acc.upcomingOrders.length; i++) {
                if (i === id) {
                    acc.upcomingOrders[i].status = status;
                    acc.upcomingOrders[i].arrDate = !!arrDate ?  new Date(arrDate).toDateString() : "";
                }
            }

          
            for (let j = 0; j < arr.length; j++) {
                if (arr[j]._id === acc._id) {
                    arr[j] = acc;
                }
            }

            return arr;
        })

        await updateOrderStats(email)
        setUsersUpdated(true)
    } 

    const addUpOrder = (newOrder) => {
      const userEmail = localStorage.getItem('email');
      
      setAllUsers(prev => {

        const arr = [...prev];

        for (let i = 0; i < arr.length; i++) {
          if (arr[i].email === userEmail) {
            const prevUpOrders = [...arr[i].upcomingOrders];
            arr[i].upcomingOrders = [...prevUpOrders, newOrder]
          }
        }

        return arr;

      })
     
      setUsersUpdated(true)
    }

    const cancelUpOrder = async (id, email) => {
      setAllUsers(prev => {

        const arr = [...prev];
        
        for (let i = 0; i < arr.length; i++) {

          if (arr[i].email === email) {

            const prevOrders = arr[i].upcomingOrders;
            arr[i].upcomingOrders = prevOrders.filter((data, index) => {
              return index !== id
            })
          }
        }

        return arr;

      })

      await updateOrderStats(email)
      setUsersUpdated(true)

    }

  return (
    <UpOrdersContext.Provider value={{upOrders, statusUpChanger, addUpOrder, cancelUpOrder, totalSales, numVisitors}}>
      {children}
    </UpOrdersContext.Provider>
  )
}

export const useUpOrders = () => {
    return useContext(UpOrdersContext);
}
