import React, {createContext, useContext} from 'react' 
import allProducts from './data/InvProData'
import useWebSocket from 'react-use-websocket'
import { v4 as uuidv4 } from 'uuid';

const StocksContext = createContext();

export default function StocksProvider({children}) {

  const [modBestSeller, setModBestSeller] = React.useState(false);
  const [isStocksUpdated, setIsStocksUpdated] = React.useState(false);
  const uri = 'ws://localhost:1200';
  const {lastJsonMessage} = useWebSocket(uri, {
    share: true
  });

  const {sendJsonMessage} = useWebSocket(uri, {
    share: true,
    filter: () => false,
    retryOnError: true,
    shouldReconnect: () => true
  });

  const changeBestSellers = (actualIndex, added, data) => {
    setModBestSeller(true);
    setStocks(prev => {
      const arr = [...prev];
      const info = {
        ...data,
        bestSeller: added
      }

      arr[actualIndex] = info;
      return arr;
    })
  }

  React.useEffect(() => {

    if (modBestSeller) {
      sendJsonMessage({modBestSeller, stocks});
      setModBestSeller(false);
    }

  }, [modBestSeller])


  const [stocks, setStocks] = React.useState([{
    proName: 'New Product',
    description: 'this is a new product',
    price: 'Up to ₱400.00',
    imgSrc: '../assets/Products Section/new-product8.png',
    index: '75603f50-ee03-4bd5-89ea-e0cd8681cb76',
    type: 'cookie',
    variants: [ { name: 'box of 7', variantPrice: '₱400.00' } ],
    actualIndex: 9
  }]);

  React.useEffect(() => {

    if (!!isStocksUpdated) {
      sendJsonMessage(stocks);
      setIsStocksUpdated(false);
    }

  }, [isStocksUpdated]);

  React.useEffect(() => {

    if (!!lastJsonMessage) {
      if (lastJsonMessage.modBestSeller) {
        setStocks(lastJsonMessage.stocks)
      } 

      if (!!lastJsonMessage[0] && !!lastJsonMessage[0].variants) {

        setStocks(lastJsonMessage);
      }

    }

  
  }, [lastJsonMessage, setStocks])

   const [isProAvail, setIsProAvail] = React.useState(true);
  
  React.useState(async () => {
    const result = await fetch('/get-stocks');
    const stocksDB = await result.json();
    setStocks(stocksDB);
    setIsProAvail(true);
    
    /* const all = [];

    for (let i = 0; i < allProducts.length; i++) {
      const uniqueId = uuidv4();
      all[i] = {...allProducts[i], index: uniqueId}
    }

    fetch('http://localhost:1200/restock', {
      method: "POST",
      headers: {
        "Content-Type": "application/json"
      },
      body: JSON.stringify(all)
    }) */
    
  }, [])


  return (
    <StocksContext.Provider value={{stocks, setStocks, isProAvail, changeBestSellers, setIsProAvail, setIsStocksUpdated}} >
      {children}
    </StocksContext.Provider>
  )
}

export const useStocks = () => {
    return useContext(StocksContext)
}
