import React, { createContext, useContext, useState } from 'react'
import useWebSocket from 'react-use-websocket';

const NotificationContext = createContext(null);

export default function NotificationsProvider({children}) {

  const [notifs, setNotifs] = useState([]);
  const [newAlert, setNewAlert] = useState(false); 

  React.useEffect(() => {
    fetch('/get-notifs', {
      method: "GET",
      headers: {
        "Content-Type": "application/json"
      }
    })
    .then(data => data.json())
    .then(data => {
      setNotifs(data);
      if (!!data.length) {
        setNewAlert(true);
      }
    })
  }, [])

  

  const uri = 'ws://localhost:1200'
  const {sendJsonMessage} = useWebSocket(uri, {
    share: true,
    filter: () => false,
    retryOnError: true,
    shouldReconnect: () => true
  });

  const {lastJsonMessage} = useWebSocket(uri, {
    share: true
  });



  const storeNotifDB = (notif) => {
    
   fetch('http://localhost:1200/store-notifs', {
    method: "POST",
    headers: {
        "Content-Type": "application/json"
    },
    body: JSON.stringify({notif})
   })

  }

  const restoreNotifDB = (notifs) => {
    fetch('http://localhost:1200/restore-notifs', {
      method: "POST",
      headers: {
        "Content-Type": "application/json"
      },
      body: JSON.stringify({notifs})
    })
  }
  
  const addNotif = (category, email, product = "", route = "/products") => {

    const info = {
        category, email, product, route
    }

    storeNotifDB(info);
    sendJsonMessage({isNotif: true, info});
  }

  const [restore, setRestore] = React.useState(false);

  const removeNotif = (index) => {

    setNotifs(prev => {
       return [...prev].sort((a, b) => notifs.indexOf(b) - notifs.indexOf(a)).filter((data, i) => i !== index);
    })

    setRestore(true);
  }

  React.useEffect(() => {
    if (restore) {
      restoreNotifDB(notifs)
      setRestore(false)
    }
  }, [restore])

  const clearNotifsDB = () => {
    fetch('http://localhost:1200/clear-notifs', {
      method: "DELETE"
    })
  }

  const clearNotifs = () => {
    setNotifs([]);
    clearNotifsDB()
  }

  React.useEffect(() => {

    if ((!!lastJsonMessage && lastJsonMessage.hasOwnProperty('isNotif') && !!lastJsonMessage.isNotif)) {
        setNotifs(prev => [...prev, lastJsonMessage.info]);
        setNewAlert(true);
    }

  }, [lastJsonMessage])


  return (
    <NotificationContext.Provider value={{notifs, addNotif, newAlert, setNewAlert, clearNotifs, removeNotif}}>
        {children}
    </NotificationContext.Provider>
  )
}

export const useNotif = () => {
    return useContext(NotificationContext)
}
