import React from 'react'
import { useRef } from 'react';
import { useUsers } from '../../UsersInfoProvider';


export default function RegisterCard({setIsLoading ,regUserInfo, showPass, navigate, setRegUserInfo, setErrMsg, setIsVisible, setSuccModalVisible, setSuccMsg, userAdder, setEmailVerVisible, setSendCode, fetchData}) {
  
  const emailRef = useRef(null);
  const passRef = useRef(null);
  const confirmPassRef = useRef(null);
  const {setAllUsers, allUsers} = useUsers();

  const errorMsgCleaner = (target) => {
   target.nextElementSibling.classList.remove('active');
  }

  const inputSave = ({target}) => {
   setRegUserInfo(prev => (
      {
         ...prev,
         [target.id]: target.value
      }
   ));

   //Reset the error message based on their input element
   errorMsgCleaner(target);
   
  }

  const newUserAcc= {email: regUserInfo.email, password: regUserInfo.password, status: false, cartProducts: [], personalInfo: {
    fullName: "",
    phoneNumber: "",
    email: regUserInfo.email,
    address: "",
    zip: "",
    city: "",
  },
  upcomingOrders: []
}

  

  const userDataValidation = () => {
   const email = emailRef.current;
   const pass = passRef.current;
   const confirmPass = confirmPassRef.current;

   const passErrMsg = pass.nextElementSibling;
   const confirmPassMsg = confirmPass.nextElementSibling;

   const emailMsg = email.nextElementSibling;
   const passMsg = pass.nextElementSibling;
   const confirmMsg = confirmPass.nextElementSibling;
 

      //email field
      if (!email.checkValidity()) {
        emailMsg.classList.add('active');
        emailMsg.innerText = "*" + email.validationMessage;
        return;
      };

      //password field
      if (!pass.checkValidity()) {
        passMsg.classList.add('active');
        passMsg.innerText = "*" + pass.validationMessage;
        return;
      };

      //confirm password field
      if (!confirmPass.checkValidity()) {
        confirmMsg.classList.add('active');
        confirmMsg.innerText = "*" + confirmPass.validationMessage;
        return;
      }

       //for matching the two passwords
       if (!confirmPassValidation(regUserInfo, confirmPassMsg)) return;
      
       //for formatting the right passwords
       if (!regexValidation(regUserInfo.password, passErrMsg)) return;

       setIsLoading(true);
       fetchData();
  }

  const regexValidation = (password, msg) => {
   
   //password must contained digits and characters
   const regexFormat = /(?=.*[a-zA-Z])(?=.*\d).{6,}/;
   
   //password must be 6 characters long
   const regexLength = /\w{6,}/;

   let checker = true;
  
    if (regexLength.test(password)) {
       if (!regexFormat.test(password)) {
        //to set the text in error modal
        setErrMsg("Password must be a combination of characters and digits.");

        //to set the visibility of an error modal
        setIsVisible(true);
        checker = false;
       } 
        
    } else {
      msg.classList.add('active');
      msg.innerText = "*input at least 6 characters";
      checker = false;
    }
    return checker;
  }

  const confirmPassValidation = ({password, confirmPass}, msg) => {
    if (password !== confirmPass) {
      msg.classList.add('active');
      msg.innerText = "*Passwords didn't match"
      return false;
    } else {
      msg.classList.remove('active');
      return true;
    }

  }

  return (
    <div className='flex flex-col gap-8 h-full justify-center items-center register'>
           <h2 className='text-4xl font-bold mb-4'>Register</h2>
           {/* <ul className='flex gap-6'>
               <li><img width="35" alt='' src='./assets/fb-icon.png'/></li>
               <li><img width="35" alt='' src='./assets/instagram-icon.png'/></li>
               <li><img width="35" alt='' src='./assets/tiktok-icon.png'/></li>
               
           </ul> */}

           <div className='w-full input-box-con flex flex-col gap-10 items-center mt-2'>
             <div className='relative border-darkBlue w-10/12 h-12 rounded-xl'>
                <input ref={emailRef} value={regUserInfo.email} onChange={inputSave} id='email' type='email' required className='absolute bg-transparent z-10 inset-0 w-full h-full px-2 rounded-xl'/>
                <p className='absolute -right-10 opacity-0 text-sm -top-6 text-red'></p>
                <label className='absolute top-2 left-2'>Email</label>
                
             </div>

             <div className='relative border-darkBlue w-10/12 h-12 rounded-xl'>
             <img onClick={showPass} width="23" className='absolute top-3 right-3 z-20 cursor-pointer' src='./assets/Login Section/eye-slash.png' alt=''/>
                <input value={regUserInfo.password} ref={passRef} onChange={inputSave} min="6" type='password' required id='password' className='absolute bg-transparent z-10 inset-0 w-full h-full px-2 rounded-xl'/>
                <p className='absolute -right-10 opacity-0 text-sm -top-6 text-red'>axax</p>
                <label className='absolute top-2 left-2'>Password</label>
                
             </div>

             <div className='relative border-darkBlue w-10/12 h-12 rounded-xl'>
             <img onClick={showPass} width="23" className='absolute top-3 right-3 z-20 cursor-pointer' alt='' src='./assets/Login Section/eye-slash.png'/> 
                <input value={regUserInfo.confirmPass} ref={confirmPassRef} onChange={inputSave} type='password' required id='confirmPass' className='absolute bg-transparent z-10 inset-0 w-full h-full px-2 rounded-xl'/>
                <p className='absolute -right-10 opacity-0 text-sm -top-6 text-red'></p>
                <label className='absolute top-2 left-2'>Confirm Password</label>
             </div>
           </div>

           <div className='flex items-center gap-5'>
           <button onClick={() => navigate('/login')} className='text-sm uppercase bg-white text-brown p-2 px-8 rounded-md border-solid border-2 border-brown lg:hidden'>Sign in</button>
           <button onClick={() => userDataValidation()} className='text-sm uppercase bg-brown text-white p-2 px-8 rounded-md'>Register</button>
           </div>

        </div>
  )
}
