import React from 'react'

export default function CancelConfirmation({handleCancel, modalActive, setModalActive}) {


  return (
    <>
    <div onClick={() => setModalActive(false)} className={`${!modalActive && "hidden"} fixed inset-0 bg-cartOverlay w-full h-full z-50`}></div>
     <div className={`${!modalActive && "scale-0 opacity-0"} bg-white w-4/5 p-5 rounded-xl flex flex-col gap-7 fixed bottom-1/2 right-1/2 translate-y-1/2 translate-x-1/2 z-50 xsm:w-80`}>
       <div>
       <p className='font-semibold select-none text-lg mb-1 lg:text-xl'>Cancel this order?</p> 
       <p className='text-sm text-gray lg:text-base'>all records of this order will be deleted.</p>
       </div>
       <div className='flex gap-5 text-sm'>
         <button onClick={handleCancel} className='bg-red text-white p-2 px-6 rounded-lg'>Yes</button>
         <button onClick={() => setModalActive(false)} className='p-2 px-6 border border-green rounded-lg'>No</button>
       </div>
     </div> 
     </>
  )
}
